header, .hero-container {
  background-color: var(--main-bg-color);
}

.header-container {
  max-width: calc(100% - 30px);
  min-width: 300px;
  height: auto;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 12px 20px;
  display: flex;
}

.ice-icon svg {
  fill: var(--default-btn-color);
}

.menu-toggle svg {
  width: 32px;
  height: 32px;
  fill: var(--white-color);
}

.header_buttons {
  gap: 32px;
  display: flex;
}

.menu-toggle {
  background-color: inherit;
  border: none;
}

.head-navigation {
  color: var(--white-color);
}

.nav-list {
  gap: 28px;
  display: flex;
}

.nav-list_element {
  letter-spacing: 0;
  text-align: left;
  color: var(--white-color);
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
}

.mobile-nav-list {
  flex-direction: column;
  align-items: center;
  gap: 28px;
  padding: 0;
  list-style: none;
  display: flex;
}

.mobile-nav-list .nav-list_element {
  letter-spacing: 0;
  text-align: center;
  color: var(--light-bg-font-color);
  font-size: 32px;
  font-weight: 700;
  line-height: 39px;
  text-decoration: none;
}

.nav-list_element__decor {
  color: var(--dark-font-color);
}

.purchase_button {
  width: 152px;
  height: 42px;
  background: var(--default-btn-color);
  border: none;
  border-radius: 10px;
  padding: 0;
}

.purchase_button p {
  text-align: center;
  justify-content: center;
  align-items: center;
  gap: 4px;
  margin: 0;
  font-size: 14px;
  font-weight: 600;
  line-height: 17px;
  display: flex;
}

.purchase_button svg {
  width: 12px;
  height: 12px;
  fill: var(--black-color);
}

.purchase_button:focus {
  color: var(--white-color);
}

.purchase_button svg:focus {
  fill: var(--white-color);
}

.purchase_button:hover, .purchase_button:focus {
  background: var(--hover-btn-color);
  border: none;
}

.mobile-fix {
  padding-top: 114px;
  padding-bottom: 88px;
}

.menu-container {
  width: 100%;
  height: auto;
  background-color: var(--main-bg-color);
  z-index: 5;
  flex-direction: column;
  align-items: center;
  padding: 0;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  transform: translateX(100%);
}

.menu-container .is-open {
  height: 100vh;
  transform: translateX(0);
}

.mobile-box {
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  padding: 12px 20px;
  display: flex;
}

.hero-container {
  max-width: 480px;
  min-width: 300px;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  padding-bottom: 0;
  display: flex;
}

.hero-left-side h1 {
  letter-spacing: 1.04px;
  text-transform: uppercase;
  flex-direction: column;
  font-family: Seymour One;
  font-size: 34px;
  display: flex;
}

.hero-left-side p {
  letter-spacing: 1.04px;
  text-transform: uppercase;
  margin: 0;
  font-size: 26px;
}

.hero-left-side {
  flex-direction: column;
  display: flex;
}

.hero-left-side_navigation {
  align-items: center;
  gap: 12px;
  padding-top: 10px;
  padding-bottom: 19px;
  display: flex;
}

.hero-left-side_navigation_button {
  background-color: inherit;
  width: 108px;
  height: 38px;
  border: 2px solid #f8d24e;
  border-radius: 8px;
}

.hero-left-side_navigation_button:hover {
  border: 2px solid var(--hover-btn-color);
}

.hero-left-side_navigation_button:focus, .hero-left-side_navigation_button p:focus {
  border: 2px solid var(--hover-btn-color);
  color: var(--white-color);
}

.hero-left-side_navigation_button:disabled {
  border: 2px solid var(--disabled-btn-colo);
}

.hero-left-side_navigation_button p {
  letter-spacing: 0;
  text-align: center;
  font-size: 12px;
  font-weight: 600;
  line-height: 15px;
}

.hero-left-side_navigation_nav {
  letter-spacing: 0;
  text-align: center;
  color: var(--default-btn-color);
  width: 114px;
  height: 24px;
  font-size: 12px;
  font-weight: 600;
  line-height: 15px;
}

.hero-left-side_navigation_nav:hover {
  color: var(--hover-btn-color);
}

.hero-left-side_navigation_nav:focus {
  color: var(--white-color);
}

.hero-left-side_navigation_nav:disabled {
  color: var(--disabled-btn-color);
}

.hero-left-side_presentation {
  width: 152px;
  display: flex;
}

.hero-left-side_presentation_description {
  letter-spacing: 0;
  text-align: left;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}

.hero-left-side_presentation_icon {
  fill: #fcfcfc;
}

.hero-right-side_presentation_hero {
  width: 100%;
  height: auto;
}

.icon-dots {
  fill: var(--default-btn-color);
  margin-top: -70px;
  margin-left: -40px;
}

@media screen and (min-width: 1200px) {
  .menu-toggle, .menu-container {
    display: none;
  }
}

@media screen and (max-width: 1199px) {
  .desktop {
    display: none;
  }
}

@media screen and (max-width: 767px) {
  .head-navigation, .tablet_fix {
    display: none;
  }
}

@media screen and (min-width: 767px) {
  .tablet {
    display: none;
  }
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .hero-right-side_presentation_hero {
    max-width: 218px;
  }
}

